import type {
  GenerateTextParams,
  IAgentRuntime,
  ModelTypeName,
} from "@elizaos/core";
import { logger, ModelType } from "@elizaos/core";
import { generateText } from "ai";
import { createOpenAIClient } from "../providers";
import {
  getSmallModel,
  getLargeModel,
  getExperimentalTelemetry,
} from "../utils/config";
import { emitModelUsageEvent } from "../utils/events";

async function generateTextByModelType(
  runtime: IAgentRuntime,
  params: GenerateTextParams,
  modelType: ModelTypeName,
  getModelFn: (runtime: IAgentRuntime) => string,
): Promise<string> {
  const openai = createOpenAIClient(runtime);
  const modelName = getModelFn(runtime);
  const experimentalTelemetry = getExperimentalTelemetry(runtime);

  logger.log(`[OpenAI] Using ${modelType} model: ${modelName}`);
  logger.log(params.prompt);

  const {
    prompt,
    stopSequences = [],
    maxTokens = 8192,
    temperature = 0.7,
    frequencyPenalty = 0.7,
    presencePenalty = 0.7,
  } = params;

  const { text: openaiResponse, usage } = await generateText({
    model: openai.languageModel(modelName),
    prompt: prompt,
    system: runtime.character.system ?? undefined,
    temperature: temperature,
    maxOutputTokens: maxTokens,
    frequencyPenalty: frequencyPenalty,
    presencePenalty: presencePenalty,
    stopSequences: stopSequences,
    experimental_telemetry: {
      isEnabled: experimentalTelemetry,
    },
  });

  if (usage) {
    emitModelUsageEvent(runtime, modelType, prompt, usage);
  }

  return openaiResponse;
}

/**
 * TEXT_SMALL model handler
 */
export async function handleTextSmall(
  runtime: IAgentRuntime,
  params: GenerateTextParams,
): Promise<string> {
  return generateTextByModelType(
    runtime,
    params,
    ModelType.TEXT_SMALL,
    getSmallModel,
  );
}

/**
 * TEXT_LARGE model handler
 */
export async function handleTextLarge(
  runtime: IAgentRuntime,
  params: GenerateTextParams,
): Promise<string> {
  return generateTextByModelType(
    runtime,
    params,
    ModelType.TEXT_LARGE,
    getLargeModel,
  );
}
